@import url(https://fonts.googleapis.com/css2?family=Texturina:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700;900&display=swap);
:root {
  --bg-color: #f4f4f4;
  --pri-color: #3f6b94;
  --pri-2-color: #ef7d32;
  --dark-bg-color: #cccccc;
  --light-blue: #719BFA;
  --lighter-black: #F2F3F8;
  --light-black: #727489;
  --dark-black: #2A3155;
  --light-purple: #0484C4;
  --lightest-blue: #92D8F4;
  --light-gray: #F0F8FB;
}

/*"Merriweather", "Serif"
"Source sans pro", "Sans-Serif"*/

*,
*::before,
*::aftrer {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

body {
  background: #f4f4f4;
  background: var(--bg-color);
}
.admin__container {
  width: 90%;
  margin: 2rem auto;
  background: var(--bg-color);
  box-shadow: .4rem .4rem .7rem white, -.4rem -.4rem .7rem #cbcbcb;
  padding: .5rem;
  position: relative;
}

.logout__button {
  position: absolute;
  right: 1rem;
  top: 1rem;
  border: none;
  width: 5rem;
  height: 2rem;
  border-radius: .5rem;
  text-align: center;
  background: var(--bg-color);
  box-shadow: .4rem .4rem .7rem white, -.4rem -.4rem .7rem #cbcbcb;
}

.create__dish--container {
  width: 80%;
  margin: 0 auto;
  padding: .4rem;
  border-radius: .5rem;
  border: 1px solid black;
}

.form__wrapper--container {
  //border: 1px solid green;
  margin: 0 auto;
  width: 90%;
  display: grid;
  grid-gap: .7rem;
  grid-template-columns: 1fr 1fr 1fr;
}

.form__wrapper input {
  margin: .5rem 0 0 0;
  padding: .4rem;
  border-radius: .5rem;
  border: none;
  background: var(--bg-color);
  box-shadow: inset .4rem .4rem .7rem white, inset -.4rem -.4rem .7rem #cbcbcb;
  outline: none;
}

.form__wrapper input:focus {
  box-shadow: inset .4rem .4rem .7rem white, inset -.4rem -.4rem .4rem var(--light-blue);
}

.create__dish--button {
  width: 8rem;
  height: 2rem;
  border: none;
  border-radius: .3rem;
  background: #146c06;
  color: white;
  margin: 2rem 0 1rem 15rem;
  font-weight: 700;
}

.display__table--container {
  width: 80%;
  margin: 2rem auto;
  border: 1px solid black;
  border-radius: .5rem;
  padding: .4rem;
}

.display__table--container table {
  width: 95%;
  margin: 1rem auto;
  //border: 1px solid black;
  border-collapse: collapse;
}

.display__table--container tr,
.display__table--container td,
.display__table--container th {
  border: 1px solid black;
  padding: .4rem;
}
.user__container {
  width: 90%;
  margin: 2rem auto;
  background: var(--bg-color);
  box-shadow: .4rem .4rem .7rem white, -.4rem -.4rem .7rem #cbcbcb;
  padding: .5rem;
}

.search__container {
  width: 80%;
  margin: 0 auto;
  padding: .4rem;
  border-radius: .5rem;
  //border: 1px solid black;
}


.search__container input {
  margin: 1rem 0 0 0;
  padding: .4rem 0 .4rem 1rem;
  border-radius: .5rem;
  width: 90%;
  height: 2.5rem;
  border: none;
  background: var(--bg-color);
  box-shadow: .3rem .3rem .4rem white, -.3rem -.3rem .4rem #cbcbcb;
  outline: none;
}

.search__container input:focus {
  box-shadow: inset .4rem .4rem .4rem white, inset -.4rem -.4rem .4rem #cbcbcb;
}

.login__container {
  width: 60%;
  text-align: center;
  margin: 2rem auto;
  background: var(--bg-color);
  box-shadow: .4rem .4rem .7rem white, -.4rem -.4rem .7rem #cbcbcb;
  padding: .5rem;
}

.login__wrapper input {
  margin: 1rem 0 0 0;
  width: 80%;
  padding: .4rem 0 .4rem 1rem;
  border-radius: .5rem;
  height: 1.5rem;
  border: none;
  background: var(--bg-color);
  box-shadow: .3rem .3rem .4rem white, -.3rem -.3rem .4rem #cbcbcb;
  outline: none;
}

.login__wrapper label {
  text-align: left;
}

.login__checkbox {
  margin: 1rem 0;
}

.login__wrapper input:focus {
  box-shadow: inset .4rem .4rem .4rem white, inset -.4rem -.4rem .4rem #cbcbcb;
}

.login__button button {
  border: none;
  padding: .4rem;
  width: 5rem;
  border-radius: .5rem;
  color: white;
  background: green;
}
